/* eslint-disable camelcase */
import { createSlice } from '@reduxjs/toolkit'
// import axios from 'axios'

export const auth = createSlice({
  name: 'auth',
  initialState: {
    user: null,
    role: [],
    currentRole: null,
    // user: {
    //   id_user: 1,
    //   name: 'Jhonarendra',
    // },
    // role: [
    //   {
    //     id_role: 1,
    //     nama_role: 'Admin',
    //     abilities: 'admin',
    //   },
    //   {
    //     id_role: 2,
    //     nama_role: 'User',
    //     abilities: 'user',
    //   },
    // ],
    // currentRole: {
    //   id_role: 2,
    //   nama_role: 'User',
    //   abilities: 'user',
    // },
  },
  reducers: {
    setUser: (state, val) => {
      const { user, role, current_role } = val.payload
      state.user = user
      state.role = role
      state.currentRole = current_role
    },
    setDummyUser: (state) => {
      state.user = {
        id_user: 1,
        name: 'Agus Putra Witama',
        email: 'putrawitama@gmail.com',
      }
      state.role = [
        {
          id_role: 1,
          nama_role: 'Admin',
          abilities: 'ramp,inspector',
        },
        {
          id_role: 2,
          nama_role: 'User',
          abilities: 'ramp,inspector',
        },
      ]
      state.currentRole = {
        id_role: 2,
        nama_role: 'User',
        abilities: 'ramp,inspector',
      }
    },
  },
})

export const { setUser, setDummyUser } = auth.actions

